*{
    margin: 0;
    padding: 0;
    overflow: hidden;
}

body{
    background-image: url("https://i.pinimg.com/originals/6f/25/31/6f253139d3d2a3c05a0b43dbdb1a15ed.png");
    background-repeat: no-repeat;
    background-size: cover;
    justify-content: center;
    display: flex;
}

.Header{
    width:100%;
    height: 50px;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    nav{
        width: 850px;
        height: 80px;
        background-color: rgb(1, 1, 44);
        display: flex;
        align-items: center;
        h2{
            color:white;
            font-weight: bold;
            margin-left:10px;
        }
    }
    .logo{
        height: 40px;
        border-radius: 50%;
        margin-left: 10px;
    }
}

.Content{
    background-color: white;
    height: 100vh;
    width:850px;
    display: flex;
}

.Sidebar{
    width:250px;
    height: 100vh;
    background-color: rgb(1, 1, 44);
    
    div{
        margin:15px;
        border-bottom: .5px solid rgb(255, 255, 255);
        height: 20px;
        padding: 10px;
        color: white;
        font-size: 18px;
        cursor: pointer;
        &.active{
            background-color: white;
            color:rgb(1, 1, 44);
            font-weight: bold;
            width:215px;
            border-top-left-radius:10px;
            border-bottom-left-radius:10px;
        }
    }

}

.tasks{
    margin: 30px;

    h1{
        color: rgb(1, 1, 44);
    }
    #tskdata{
        color: black;
        font-weight: bold;
        font-size:20px;
        margin: 5px;
        padding: 5px;
        background-color: rgb(192, 97, 236);
        display: flex;
        justify-content: space-between;
    }
    #tskdate{
        color: rgb(0, 0, 0);
        font-size:10px;
        margin-left: 10px;
    }
    .add-task-btn{
        background-color: rgb(103, 30, 136);
        color:white;
        width:80px;
        border-radius: 10px;
        padding-left: 5px;

        span{
            font-weight: bold;
        }
    }
}

.task-dialogue{
    margin-top: 10px;
    margin-bottom: 10px;
    .tskinp{
        width:400px;
        height:28px;
        margin-bottom: 10px;
    }
    
     
}

.task-btn-action-container{
    display: flex;
    justify-content: space-between;
    button{
        width: 80px;
        height: 35px;
        border-radius: 10px;
        background-color: rgb(103, 30, 136);
        color:white;
        margin-right: 5px;
        border:none;
    }
    .cancel-btn{
        background-color: rgb(236, 124, 124);
        //border: 2px solid black;
        color: black;
    }
}